.current_wrapper{
   
    height: 100vh;
    display: flex;
   justify-content: space-between;
}
.film_right_wrapper{
    display: flex;
    flex-direction: column;
    color: white;
    font-weight: 600;
    font-size: 19px;
    text-align: center;
}

.film_left_wrapper{
    width: 50%;
    height: 600px;
    
    display: flex;
    flex-direction: column;
}
.film_name_ru{
    margin-top: 20px;
    font-style: italic;
    font-weight: 700;
    color: white;
    font-size: 32px;
    text-align: center;
    margin-bottom: 10px;
}
.film_name_orig{
    margin-bottom: 20px;
}
.film_name_eng{
    margin-bottom: 20px;
    font-weight: 700;
    color: white;
    font-size: 30px;
    text-align: center;
}
.current_poster{
    width: 300px;
    height: 450px;
    border-radius: 10px;
    margin: 0 auto;
    margin-bottom: 30px;
    margin-top: 20px    ;
}
.film_rating{
    margin-bottom: 100px;
}
.film_right_wrapper{
    width: 50%;
    height: 600px;
    margin-right: 20px;
}
.film_description{
    font-weight: 600;
    font-size: 21px;
    color: white;
}
.film_length{
    color: white;
    font-weight: 600;
    font-size: 19px;
    text-align: center;
}
.film_year{
    margin-bottom: 20px;
    color: white;
    font-weight: 600;
    font-size: 19px;
    text-align: center;
}
.btn{
    margin-left: auto;
    margin-right: auto;
    width: 150px;
    position: relative;
    display: block;
    color: white;
    font-size: 14px;
    text-decoration: none;
    text-align: center;
    border: 2px solid #e88428 ;
    padding: 14px 60px;
    text-transform: uppercase;
    overflow: hidden;
    transition: 1s all ease;
  }
  .btn::before{
    background: green;
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: -1;
    transition: all 0.6s ease;
  }

.film_trailer_button::before{
    width: 50%;
    height: 0%;
    transform: translate(-50%,-50%) rotate(45deg);
  }
  .film_trailer_button:hover::before{
    height: 380%;
  }
  
.film_length{
    margin-bottom: 30px;
}
